@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed:400,700);
html,
body {
  height: 100%; }

body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff; }

#root {
  height: 100%; }

.container {
  min-height: 100%;
  display: grid;
  grid-template-rows: 5% auto 8%; }

.template {
  display: grid;
  grid-template-columns: 20% 60% 20%;
  grid-auto-rows: -webkit-max-content;
  grid-auto-rows: max-content;
  grid-template-areas: '. title .' '. message .' '. form .'; }
  @media only screen and (max-width: 880px) {
    .template {
      grid-template-columns: 5% 90% 5%; } }
  @media only screen and (min-width: 1500px) {
    .template {
      grid-template-columns: 30% 40% 30%; } }
  .template .title {
    grid-area: title; }
  .template .message {
    grid-area: message;
    font-weight: 300;
    font-size: 1.25em;
    line-height: 1.5em;
    padding: 0.5em;
    border: 2px solid #eceff1;
    background-color: transparent;
    align-self: start;
    margin: 0em 0em 25px;
    white-space: pre-line; }
  .template .form {
    grid-area: form;
    display: grid;
    font-size: 1.15em;
    grid-template-columns: 120px auto;
    grid-row-gap: 15px;
    align-self: start;
    margin-bottom: 1em; }
    .template .form .label {
      align-self: center; }
    .template .form select.form-control,
    .template .form .form-control {
      padding-left: 5px;
      border-radius: 5px;
      border: 1px solid #eceff1;
      font-size: 1.15em;
      width: -webkit-fill-available;
      height: 35px; }
      .template .form select.form-control:focus,
      .template .form .form-control:focus {
        border: 1px solid #ff9800;
        box-shadow: none;
        outline: 0 none; }
    .template .form .copy-button {
      grid-column: span 2; }
      .template .form .copy-button button.btn {
        font-size: 1em;
        color: #ff9800;
        background-color: #ffffff;
        border: 1px solid #ff9800;
        border-radius: 3px;
        height: 35px;
        width: -webkit-fill-available; }
        .template .form .copy-button button.btn:hover {
          color: #ffffff;
          background-color: #ff9800;
          box-shadow: none;
          outline: 0 none;
          border: none; }

.header {
  display: grid;
  grid-template-areas: 'left right';
  padding: 0px 8px;
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase; }
  .header a {
    color: #212121;
    text-decoration: none; }
  .header a:hover {
    font-weight: 700;
    border-bottom: 1px dotted #212121; }
  .header a:active {
    color: #0000ff; }
  .header .left {
    grid-area: left;
    align-self: center; }
  .header .right {
    grid-area: right;
    align-self: center;
    justify-self: end; }
    .header .right a {
      display: inline-block;
      margin: 0px 5px; }

.footer {
  display: grid;
  background-color: #eceff1;
  grid-template-columns: 20% 60% 20%; }
  @media only screen and (max-width: 880px) {
    .footer {
      grid-template-columns: 5% 90% 5%; } }
  @media only screen and (min-width: 1500px) {
    .footer {
      grid-template-columns: 30% 40% 30%; } }
  .footer .text {
    grid-column-start: 2;
    grid-column-end: 3;
    justify-self: start;
    align-self: center; }

